.container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex: 1;
  max-height: 960px;
}

.userPanel {
  display: flex;
  flex-direction: column;
  font-weight: 600;
  flex: 1;
  gap: 16px;
}

.navLabel {
  font-size: 14px;
  font-weight: 600;
  line-height: calc(17px / 14px);
  color: #7e7d82;
  margin-bottom: 32px;
}

.navLink {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;

  font-family: var(--primary-font);
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  text-decoration: none;
  color: var(--not-active-item-sidebar-text-color);
  padding: 12px;
  gap: 8px;

  & > svg {
    width: 24px;
    height: 24px;
    stroke: var(--not-active-item-sidebar-text-color);
    // fill: none;
  }
}

.activeLink {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;

  font-family: var(--primary-font);
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  text-decoration: none;

  padding: 12px;
  gap: 8px;
  color: var(--accent-text-color);

  background: var(--active-item-sidebar-background);
  border-radius: 8px;

  & > svg {
    width: 24px;
    height: 24px;
    stroke: var(--accent-text-color);
    // fill: none;
  }
}

.button {
  display: inline-flex;
  align-items: center;
  gap: 11px;
  font-weight: 600;
  font-size: 18px;
  line-height: 24px;
  color: var(--button-text-color);
  padding: 16px 23px;
  background-color: var(--button-background-color);
  box-shadow: 4px 2px 16px rgba(136, 165, 191, 0.48);
  border-radius: 16px;
  width: fit-content;
  // margin-top: auto;

  & > svg {
    width: 20px;
    height: 20px;
    stroke: #ffffff;
    // fill: none;
  }
}

@media screen and (max-width: 768px) {
  .navLabel {
    font-size: 14px;
    line-height: calc(15px / 12px);
    margin-bottom: 32px;
  }

  .navItem {
    gap: 8px;
    font-size: 16px;
    line-height: calc(19px / 16px);
    padding: 16px 20px;
  }

  .button {
    gap: 6px;
    padding: 14px 28px;
  }
}

@media screen and (max-width: 375px) {
  .navLabel {
    font-size: 12px;
    line-height: calc(15px / 12px);
    margin-bottom: 24px;
  }

  .navItem {
    font-size: 14px;
    line-height: calc(17px / 14px);
    padding: 10px 14px;

    & > svg {
      width: 20px;
      height: 20px;
    }

    &:hover,
    &:focus,
    &.selected {
      border-radius: 8px;

      & > svg {
      }
    }
  }

  .button {
    gap: 6px;
    font-weight: 600;
    font-size: 14px;
    line-height: calc(18px / 14px);
    padding: 14px 28px;

    & > svg {
      width: 18px;
      height: 18px;
    }
  }
}
